<template>
    <div class="system-settings">
        <el-collapse v-model="activeName" accordion class="settings-box">
            <el-collapse-item :title="$L('语言设置')" name="lang">
                <div class="settings-item" :class="{mainFontColor:index===lang}" v-for="(item,index) in langList" @click="changeLang(index)">
                    <i class="el-icon-check icon"></i>
                    <span class="text">{{ item.name }}</span>
                </div>
            </el-collapse-item>
        </el-collapse>
        <div class="logout mainBgColor" @click="logout">{{$L('退出当前账户')}}</div>
    </div>
</template>

<script>
    export default {
        name: "systemSettings",
        inject: ["cleanHeartbeat","reload"],
        data() {
            return {
                activeName: 'lang',
                langList:this.$C('langList', 'lang'),
                lang:this.$lang()
            };
        },
        created() {
            if(!this.$isMobile()){
                this.$router.push({name:'accountSafety'});
            }
        },
        methods:{
            changeLang(id){
                this.$lang(id);
                this.reload();
            },
            logout() {
                const _self = this;
                _self.$confirm(_self.$L('确认退出登录吗？'), _self.$L('退出登录'), {
                    confirmButtonText: _self.$L('确定'),
                    cancelButtonText: _self.$L('取消'),
                    type: 'warning',
                    distinguishCancelAndClose: true
                }).then(() => {
                    _self.cleanHeartbeat();
                    _self.$router.push({name:'logout'});
                }).catch(action => {

                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    @import '../../assets/css/common/lib.scss';
    .system-settings{
        /deep/ .el-collapse{
            border-top: none;
            .el-collapse-item__header{
                font-size: 14px;
            }
        }
        .settings-box{
            padding: 0 20px;
            .settings-item{
                padding-left: 25px;
                line-height: 35px;

                .icon{
                    margin-right: 10px;
                    opacity: 0;
                }

                &.mainFontColor{
                    .icon{
                        opacity: 1;
                    }
                }
            }
        }

        .logout{
            position: absolute;
            width: 90%;
            bottom: 20px;
            left: 50%;
            transform: translateX(-50%);
            line-height: 45px;
            text-align: center;
            color: #ffffff;
            font-size: 14px;

            &:active{
                opacity: 0.8;
            }
        }
    }
</style>
<style>
    .el-message-box {
        width: 94% !important;
    }
</style>